import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
	state: {
		checkLogin: false,  // onLaunch,,,
		hasLogin: false,
		loginUser: null,
		needLoginAlert: false,  // ,me,meonShow,false
		// loginUser: {
		// 	"avatarUrl": "",
		// 	"city": "",
		// 	"country": "",
		// 	"gender": 0,
		// 	"language": "",
		// 	"nickName": "",
		// 	"province": ""
		// }
	},
	mutations: {
		CHECK_LOGIN(state) {
			console.log("vuex ");
			state.checkLogin = true
		},
		LOGIN(state) {
			console.log("vuex ");
			state.hasLogin = true;
		},
		LOGOUT(state) {
			console.log("vuex ");
			state.hasLogin = false;
		},
		SET_USERINFO(state, userInfo) {
			state.loginUser = userInfo;
		},
		NEED_LOGIN_ALERT(state, value) {
			state.needLoginAlert = value
		}
	}
})

export default store